body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.segment-grid-container {
  border: none;
  box-shadow: none;
}

.board-row {
  width: 1129px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.square {
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 48px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 48px;
}

.square:focus {
  outline: navy;
}

.dark-square {
  background-color: darkcyan;
}

.light-square {
  background-color: lightgray;
}

.highlighted-square,
.highlighted-square-red,
.highlighted-square-green {
  border-style: solid;
  border-width: 2px;
}

.highlighted-square-red {
  background-color: crimson;
}

.highlighted-square-green {
  background-color: lightgreen;
}

.highlighted-square-in-ranged-attack {
  opacity: 0.5;
}

.health-box {
  display: block;
  left: -20px;
  position: relative;
  background-color: red;
  height: 30px;
  width: 4px;
  margin: 0 auto;
  border: solid 0.5px #aaa;
}

.remaining-health {
  display: block;
  left: 0.5px;
  bottom: 0.5px;
  position: absolute;
  background-color: #007f00;
  width: 3px;
}

.pointer-icon {
  cursor: pointer;
}

.boots-icon {
  cursor: url(./resources/mouseHoverIcons/boots.cur),auto;
}

.sword-icon {
  cursor: url(./resources/mouseHoverIcons/sword.cur),auto;
}

.bow-icon {
  cursor: url(./resources/mouseHoverIcons/bow.cur),auto;
}

.current-turn-text {
  width: 1129px;
  padding: 5px;
  font-weight: bold;
  background-color: #166859;
}

